.App {
  text-align: center;
}

button:disabled {
  background-color: darkgrey !important;
}

.buttonRow {
  display:  flex;
  padding: 10px 0;
  width: 100%;
  text-align: center;
  justify-content: center;
}

.buttonContainer {
  display:  flex;
  padding: 10px 0;
  width: 100%;
  text-align: center;
  justify-content: space-between;
  max-width: 400px;
  
}

.previewImg {
  padding-top: 40px;
  text-align: center;
  justify-content: center;
}

.buttonUpload {
  
  border: none;
  outline: none;
  color: #fff;
  padding: 14px;
  width: 100%;
  font-size: 22px;
  cursor: pointer
}

.resultImg {
  margin-top: 20px;
  text-align: center;
  justify-content: center;
}

.modalCamera {
  text-align: center;
  justify-content: center;
  width: 700px;
  height: 700px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

